
import { defineComponent } from "vue";
import Message from "./Message.vue";
import IProduto from "../interface/IProduto";

export default defineComponent({
  name: "BurguerForm",
  components: {
    Message,
  },
  data() {
    return {
      paes: [] as IProduto[],
      carnes: [] as IProduto[],
      opcionaisdata: [] as IProduto[],
      nome: "" as String,
      pao: {} as IProduto | "",
      carne: {} as IProduto | "",
      opcionais: [] as IProduto[],
      msg: "" as String | undefined,
      tipo: "",
      tipoMsg:"" as String
    };
  },
  methods: {
    async getIngredients() {
      const req = await fetch("http://localhost:3000/ingredientes");
      const data = await req.json();

      this.paes = data.paes;
      this.carnes = data.carnes;
      this.opcionaisdata = data.opcionais;

      this.nome = "";
      this.carne = "";
      this.pao = "";
    },
    validaFormulario() {
      console.log(this.carne);
      if (
        this.nome != "" &&
        this.pao.toString() != "" &&
        this.carne.toString() != ""
      ) {
        return true;
      } else {
        return false;
      }
    },
    async createBurguer(e: Event) {
      e.preventDefault();

      if (this.validaFormulario()) {
        const data = {
          nome: this.nome,
          carne: this.carne,
          pao: this.pao,
          opcionais: Array.from(this.opcionais),
          status: "Solicitado",
        };

        const dataJson = JSON.stringify(data);

        const req = await fetch("http://localhost:3000/burguers", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: dataJson,
        });

        const res = await req.json();
        this.nome = "";
        this.carne = "";
        this.pao = "";
        this.opcionais = [];

        this.msg = `Pedido Nº ${res.id} realizado com sucesso!`;
        this.tipoMsg = 'message-container'

        setTimeout(() => {
          this.msg = "";
        }, 3000);
      } else {
        this.msg = "Favor preencher todos os campos";
        this.tipoMsg = 'message-container-erro'

        setTimeout(() => {
          this.msg = "";
        }, 3000);
      }
    },
  },
  mounted() {
    this.getIngredients();
  },
});
